import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import Header from "./HeaderContainer";
import DetailHeader from "../components/detail/header";
import Comment from "../components/comment";
import Button from "../components/button";
import api from "../utils/api";
import { classTime } from "../utils/static";
import '../styles/classdetail.scss';
import backImg from '../images/global/btn-back@2x.png';


class MyclassDetailContainer extends Component {
  constructor(props) {
    super(props);
  
    this.state = {
      class_info: {
        dates: [{
          class_id: 1017,
          date: "2017-11-20",
          day: "Monday",
          end_time: "12:30:00",
          inning: 1,
          minus_time: 0,
          receipts_uploaded: false,
          start_time: "09:00:00",
        },
        {
          class_id: 1018,
          date: "2017-11-21",
          day: "Monday",
          end_time: "12:30:00",
          inning: 2,
          minus_time: 0,
          receipts_uploaded: false,
          start_time: "09:00:00",
        },
        {
          class_id: 1019,
          date: "2017-11-22",
          day: "Monday",
          end_time: "12:30:00",
          inning: 3,
          minus_time: 0,
          receipts_uploaded: false,
          start_time: "09:00:00",
        }],
        education_target: "2",
        interview: "단기 담당교사 인터뷰",
        order_option_comment: "",
        region: "어썸광역시",
        school: "임시중학교",
        school_address: "<p><span style='font-family: Arial; font-size: 13px; text-align: center; white-space: pre-wrap;'>어썸시 어썸구 어썸동 어썸로 30</span><br></p>",
        school_classes: 6,
        total_student_number: 180,
      },
      feedback: {
        avg_satisfaction: false,
        class_num: false,
        course_feedback: false,
        feedback_images: false,
        mentor_comment: false,
        students_survey: false,
      },
      classId: window.location.getParameterByName('id'),
    }
    this.goBack = this.goBack.bind(this);
  }

  async componentDidMount() {
    const { changeHeader, viewMenu } = this.props;
    const { classId } = this.state;
    const response = await api('get',`/api/v1/mentor/my_classes/${window.location.getParameterByName('id')}`);
    if(!response) return;
    const content = response.data.data;
    this.setState({
      class_info: content.class_info.details,
      feedback: (content.feedback === "No Feedback Uploaded" ? {
        avg_satisfaction: false,
        class_num: false,
        course_feedback: false,
        feedback_images: false,
        mentor_comment: false,
        students_survey: false,
      } : content.feedback),
    });
  }

  goBack(){
    const {history} = this.props;
    history.push('/myclass');
  }

  render() {
    const {class_info, feedback, classId} = this.state;
    const {avg_satisfaction, class_num, course_feedback, feedback_images, mentor_comment, students_survey}=feedback;
    return (
      <div className="container-wrapper">
        <div className="header-container">
          <div className="back-btn" onClick={this.goBack}>
            <img src={backImg} />
          </div>
          <div className="title">나의 수업 상세</div>
        </div>
        <div className="classdetail-container child-container">
          <div className="class-container">
            <div className="detail-header">수업 상세 정보</div>
            <table className="detail-table">
              <tbody>
                <tr>
                  <td className="left-td">지역구분</td>
                  <td className="right-td">{class_info.region}</td>
                </tr>
                <tr>
                  <td className="left-td">상세주소</td>
                  <td className="right-td">{class_info.school_address.replace(/(<([^>]+)>)/ig,"")}</td>
                </tr>
                <tr>
                  <td className="left-td">학교</td>
                  <td className="right-td">{class_info.school}</td>
                </tr>
                <tr>
                  <td className="left-td">학년</td>
                  <td className="right-td">
                    {class_info.education_target}
                    학년
                  </td>
                </tr>
                <tr>
                  <td className="left-td">학급수</td>
                  <td className="right-td">
                    {class_info.school_classes}
                    학급
                  </td>
                </tr>
                <tr>
                  <td className="left-td">총 학생 수</td>
                  <td className="right-td">{class_info.total_student_number}</td>
                </tr>
                <tr>
                  <td className="left-td">인터뷰</td>
                  <td className="right-td">{class_info.interview}</td>
                </tr>
                <tr>
                  <td className="left-td">기타사항</td>
                  <td className="right-td">{class_info.order_option_comment}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div className="class-container">
            <div className="detail-header">수업 시간</div>
            <div className="classlist-wrapper">
              {
                class_info.dates.map((item, index) => (
                  <div key={index} className="class-wrapper">
                    <div className="school-name">{class_info.school}</div>
                    <div className="class-info">
                      #
                      {item.inning}
                      회차 (
                      {item.date}
                      ) 
                      {` ${classTime(item.start_time)} `}
                      -
                      {` ${classTime(item.end_time)}`}
                    </div>
                    <Link to={`/myclass/detail/calculate?id=${item.class_id}`}>
                      <button className="payment-btn" type="submit">재료비 / 교통비 / 기타정산 신청</button>
                    </Link>
                  </div>
                ))
              }
            </div>
          </div>
          <div className="class-container">
            <div className="detail-header">
              <div>보고서 작성</div>
              <div className="detail-subheader">보고서 작성이 완료되면, 정산이 시작됩니다.</div>
            </div>
            <div className="report-list">
              <Button
                flag={students_survey}
                title="학생 설문조사"
                url="survey"
                classId={classId}
              />
              <Button
                flag={avg_satisfaction}
                title="평균 만족도 점수"
                url="satisfaction"
                classId={classId}
              />
              <Button
                flag={feedback_images}
                title="활동사진"
                url="images"
                classId={classId}
              />

              <Button
                flag={class_num}
                title="담당 반"
                url="classnum"
                classId={classId}
              />
              <Button
                flag={mentor_comment}
                title="수강 후 강사 의견"
                url="review"
                classId={classId}
              />
              <Button
                flag={course_feedback}
                title="수업 피드백 설문"
                url="feedback"
                classId={classId}
              />
            </div>
          </div>
          <div className="divider" />
          <button className="report-button" type="submit">결과보고서 전송하기</button>
        </div>
      </div>
    )
  }
}

export default MyclassDetailContainer;
